export default class SmsData {
  randomMobile: string
  randomDateTime: string
  randomColorArr: string
  sms: string
  constructor (randomMobile: string, randomDateTime: string, randomColorArr: string, sms: string) {
    this.randomMobile = randomMobile
    this.randomDateTime = randomDateTime
    this.randomColorArr = randomColorArr
    this.sms = sms
  }
}
