
import { Vue, Options } from 'vue-class-component'
import SmsItem from '../components/SmsItem.vue'
import SmsCommon from '@/js/SmsCommon'
import SmsData from '@/js/SmsData'
import $ from 'jquery'

@Options({
  components: { SmsItem }
})
export default class SmsScroll extends Vue {
  smsObjs = new Array<SmsData>()
  tempSmsData = new Array<SmsData>()
  smsJson = require('../assets/sms.json')

  mounted (): void {
    const smsCommon = new SmsCommon()
    for (const smsJsonKey in this.smsJson) {
      this.tempSmsData.push(new SmsData(
        smsCommon.randomMobile(), smsCommon.randomDateTime(), smsCommon.randomColorArr(), this.smsJson[smsJsonKey].sms
      ))
    }
    let i = 0
    setInterval(() => {
      const obj = this.tempSmsData[i]
      this.smsObjs.splice(0, 0, obj)
      // this.scrollToBottom()
      i++
      if (i >= this.tempSmsData.length) {
        i = 0
      }
    }, 3500)
  }

  /* private scrollToBottom () {
    const $container = $('#ssContainer')
    console.log($container)
    $container.animate({ scrollTop: $container.height() }, 500)
  } */
}
