export default class SmsCommon {
  curDate = new Date()
  curYear = this.curDate.getFullYear()
  curMonth = this.curDate.getMonth() + 1
  curMonthDay = this.curDate.getDate()
  curWeekDay = this.curDate.getDay()
  colorArr = [
    '#FF63B8',
    '#EE675C',
    '#AF5CF7',
    '#5BB974',
    '#FCC934',
    '#FA903E'
  ]

  /**
   * 随机颜色
   * @private
   */
  randomColorArr (): string {
    return 'background: ' + this.colorArr[this.randomNum(0, this.colorArr.length)]
  }

  /**
   * 随机日期
   * @private
   */
  randomDateTime (): string {
    // 当前日期
    const randomMonthDay = this.randomMonthDay()
    let timeStr = '星期' + '日一二三四五六'.charAt(new Date(this.curYear, this.curMonth - 1, randomMonthDay).getDay())
    const absDayDifference = Math.abs(this.curMonthDay - randomMonthDay)
    if (absDayDifference > 7 && absDayDifference !== 0) {
      // 非本周
      timeStr = this.curMonth + '月' + randomMonthDay + '日' + timeStr
    }
    // 随机小时和时间
    timeStr += '·' + this.randomHHmm()
    return timeStr
  }

  /**
   * 生成当月随机某天
   * @private
   */
  randomMonthDay (): number {
    const maxDay = new Date(this.curYear, this.curMonth, 0).getDate()
    const rn = this.randomNum(1, maxDay)
    return rn
  }

  /**
   * 随机时间
   * @private
   */
  randomHHmm (): string {
    let hh = String(this.randomNum(4, 22))
    let mm = String(this.randomNum(1, 59))
    if (hh.length <= 1) {
      hh = '0' + hh
    }
    if (mm.length <= 1) {
      mm = '0' + mm
    }
    return hh + ':' + mm
  }

  randomMobile (): string {
    const prefixArray = ['130', '131', '132', '133', '135', '137', '138', '170', '187', '189']
    const i = parseInt(String(10 * Math.random()))
    let prefix = prefixArray[i]
    for (let j = 0; j < 8; j++) {
      prefix = prefix + Math.floor(Math.random() * 10)
    }
    return prefix
  }

  randomNum (minNum: number, maxNum: number): number {
    switch (arguments.length) {
      case 1:
        return parseInt(String(Math.random() * minNum + 1), 10)
      case 2:
        return parseInt(String(Math.random() * (maxNum - minNum + 1) + minNum), 10)
      default:
        return 0
    }
  }
}
